* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: poppins;
}
.content {
  max-width: 900px;
  margin: 0 auto;
  padding: 0 100px;
}
.back {
  background: linear-gradient(90deg, #672280 1.18%, #a626d3 100%);
  height: 65px;
}
.header {
  color: white;
  height: 65px;

  display: flex;
  justify-content: space-between;

  align-items: center;
}
.header h2 {
  cursor: pointer;
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  text-align: right;

  color: #ffffff;
}
.logo {
  cursor: pointer;
  display: flex;
  align-items: center;
}

.logo h1 {
  margin-left: 10px;
  font-style: normal;
  font-weight: bold;
  font-size: 20.7482px;
  line-height: 24px;
  letter-spacing: -0.1em;

  color: #ffffff;
}
.inputs {
  font-family: poppins;
  padding-top: 40px;
  padding-bottom: 0;
  display: flex;
  justify-content: space-between;
}
.inputs input {
  padding: 0 20px;
  width: 230px;
  height: 35px;
  font-family: poppins;
  outline: none;
  border: 1px solid #d5d4d8;
  box-sizing: border-box;
  border-radius: 5px;
}
.submit {
  cursor: pointer;
  margin-top: 10px;
  color: white;

  width: 477px;
  height: 40px;
  border: none;
  background: linear-gradient(90.41deg, #711f8d 1.14%, #a818da 100%);
  border-radius: 5px;
  font-style: normal;
  font-weight: bold;
}
.Main-content img {
  max-width: 477px;
  border-radius: 5px;
  margin-top: 32px;
}
.Main-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.meme {
  position: relative;
}

.meme--image {
  max-width: 100%;
  border-radius: 3px;
}

.meme--text {
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000,
    -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000,
    2px 2px 5px #000;
}

.bottom {
  bottom: 0;
}

.top {
  top: 0;
}
@media screen and (max-width: 578px) {
  .content {
    padding: 0 34px;
  }

  .form {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }
  .inputs {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }
  .inputs input {
    margin-top: 12px;
    width: auto;
  }
  .submit {
    width: auto;
  }

  .Main-content img {
    max-width: 100%;
  }
  .meme {
    padding: 0;
  }
}
